<template>
  <div>
    <app-header
      showBack="true"
      title="申请售后"
    ></app-header>
    <div style="padding-top: 50px; background-color: #fff;">
      <mt-field label="退款理由" placeholder="请填写您的退款理由" type="textarea" rows="4" v-model="reason"></mt-field>
      <div class="imgs" id="imgs">
        <!-- <img src="/static/img/a.jpg" alt="" style="width: 5rem;">
        <img src="/static/img/a.jpg" alt="" style="width: 5rem;"> -->
      </div>
      <div class="ui-title">
        请上传图片
      </div>
      <div class="clearfix">
        <div class="uploader-box" style="width: 3rem; height: 3rem;">
          <input type="file" ref="resource" id="file" @change="uploadFile">
        </div>
      </div>
      <mt-button type="primary" size="large" style="margin-top: 1rem" v-on:click.native="submitReason">提交</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import {uploadImg, submitPayRefundApply} from '../../../api/home.js'
export default {
  name: 'afterSales',
  data () {
    return {
      reason: '',
      imgs: []
    }
  },
  mounted () {
    this.$on('clickToOrder', () => {
      this.$router.replace('/order')
    })
  },
  methods: {
    uploadFile () {
      let formData = new FormData()
      formData.append('0', this.$refs.resource.files[0])
      uploadImg(formData)
        .then(data => {
          if (data.state) {
            let img = document.createElement('img')
            img.style.width = '5rem'
            img.setAttribute('src', data.data.url)
            document.getElementById('imgs').appendChild(img)
            this.imgs.push(data.data.url)
          } else {
            // Toast(data.errormsg || '上传失败')
            this.$toast(data.errormsg || '上传失败')
          }
        })
        .catch(err => console.log(err))
    },
    submitReason () {
      if (!this.reason) return this.$toast('必须输入售后原因')
      submitPayRefundApply(this.$route.params.id, this.imgs.join(','), this.reason)
        .then(data => {
          if (data.state) {
            this.$toast('申请成功, 请等待客服处理')
            this.$router.replace('/order')
          } else {
            this.$toast(data.errormsg || '网络繁忙，请重试')
          }
        })
    }
  },
  components: {AppHeader}
}
</script>



